import React, {useState, useEffect} from 'react';
import 'bootstrap/dist/css/bootstrap.min.css';
import Home from './Components/Home/Home';
import Toolbar from './Components/Toolbar/Toolbar'
import SideDrawer from './Components/SideDrawer/SideDrawer'
import Backdrop from './Components/BackDrop/Backdrop';
import { useDarkMode } from "./Components/Dark/Darkmode";
import { lightTheme, darkTheme } from "./Components/Dark/Theme";
import Toggle from './Components/Dark/Toggle';
import { ThemeProvider } from "styled-components";
import { GlobalStyles } from "./Components/Dark/Global";
import {BrowserRouter as Router, Switch, Route, BrowserRouter} from 'react-router-dom';

function App() {
  const [sideDrawerOpen, setSideDrawerOpen] = useState(false)
  function drawerToggleClickHandler(){
    setSideDrawerOpen(!sideDrawerOpen)
  }
  function backdropClickHandler(){
    setSideDrawerOpen(!sideDrawerOpen)
  }
  let backdrop;

  if(sideDrawerOpen){
    backdrop = <Backdrop click={backdropClickHandler} />
  }


  const [theme, toggleTheme] = useDarkMode("dark");

  return (
    <React.Fragment>
      <ThemeProvider theme={theme === "dark" ? lightTheme : darkTheme}>
      <GlobalStyles />
      <Router>
        <Toolbar drawerClickHandler={drawerToggleClickHandler} onToggle={toggleTheme} />
        {/* <Toggle onToggle={toggleTheme}>Toggle theme</Toggle> */}
        <SideDrawer show={sideDrawerOpen} drawerClickHandler={drawerToggleClickHandler} />
        {backdrop}
        <Switch>
          <Route path="/" exact component={Home} />
          {/* <Route path="/portfolio" exact component={Work} />
          <Route path="/about" component={About} />
          <Route path="/contact" component={Contact} /> */}
        </Switch>
      </Router>
      </ThemeProvider>
    </React.Fragment>
  );
}

export default App;
